import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Hello from './panels/Hello';
import Dindex from './panels/Dostopr';
import Other from './panels/Other';

// –ø—Ä–æ–º—ã—à–ª–µ–Ω–Ω—ã–µ
import PromIndex from './panels/prom/Index';
import Surgut from './panels/prom/Surgut';
import Niznevartovsk from './panels/prom/Niznevartovsk';
import Omsk from './panels/prom/Omsk';
import Perm from './panels/prom/Perm';
import Ufa from './panels/prom/Ufa';
import Norilsk from './panels/prom/Norilsk';
import Chelabinsk from './panels/prom/Chelabinsk';
import Novokuznetsk from './panels/prom/Novokuznetsk';

// –ø–æ—Ä—Ç–æ–≤—ã–µ
import PortIndex from './panels/ports/Index';
import Novorossiysk from './panels/ports/Novorossiysk';
import Primorsk from './panels/ports/Primorsk';
import Nahodka from './panels/ports/Nahodka';
import PeterburgPort from './panels/ports/Peterburg'

// –∫—É—Ä–æ—Ä—Ç–Ω—ã–µ
import KurortIndex from './panels/curorts/Index';
import Kostroma from './panels/curorts/Kostroma';
import Yalta from './panels/curorts/Yalta';
import Sochi from './panels/curorts/Sochi';

// –ø–µ—Ç–µ—Ä–±—É—Ä–≥ –∏ –º–æ—Å–∫–≤–∞ :)
import Moscow from './panels/prom/Moscow';
import Peterburg from './panels/prom/Peterburg';


//–æ–±–ª–∞—Å—Ç–∏
import MosObl from './panels/oblasti/Moscow';
import LenObl from './panels/oblasti/Leningrad';
import OmskObl from './panels/oblasti/Omsk';
import PermObl from './panels/oblasti/Perm';
import Baskortostan from './panels/oblasti/Baskortostan';
import NorilObl from './panels/oblasti/Norilsk';
import ChelaObl from './panels/oblasti/Chelyabinsk';
import KrasnodarK_novoross from './panels/oblasti/Krasnodarskiy_novoross';
import KrasnodarK_sochi from './panels/oblasti/Krasnodarskiy_sochi';
import PrimObl from './panels/oblasti/Primorsk';
import Krym from './panels/oblasti/Krym';
import KostromObl from './panels/oblasti/Kostroma';
import TumenObl from './panels/oblasti/Tumen';


// –¥–æ—Å—Ç–æ–ø—Ä–∏–º–∏–µ—á–∞—Ç–µ–ª—å–Ω–æ—Å—Ç–∏
	// –º–æ—Å–∫–≤–∞
		import MosIndex from './panels/Dostop/Moscow/Index';
		import MoscKreml from './panels/Dostop/Moscow/Cremlin';
	// —Å–ø–±
		import SpbIndex from './panels/Dostop/Spb/Index';
		import SpbZinger from './panels/Dostop/Spb/Zinger';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState();

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Hello id='hello' go={go} />
			<Dindex id='dostIndex' go={go} />
			<PromIndex id='prom' go={go} />
			<PortIndex id='port' go={go} />
			<KurortIndex id='kur' go={go} />
			<Other id='other' go={go} />
			<Moscow id='moscow' go={go} />
			<Peterburg id='peterburg' go={go} />
			<Surgut id='surgut' go={go} />
			<Niznevartovsk id='niznevartovsk' go={go} />
			<Omsk id='omsk' go={go} />
			<Perm id='perm' go={go} />
			<Ufa id='ufa' go={go} />
			<Norilsk id='norilsk' go={go} />
			<Chelabinsk id='chelabinsk' go={go} />
			<Novokuznetsk id='novokuznetsk' go={go} />

			<Novorossiysk id="novorossiysk" go={go} />
			<Primorsk id="primorsk" go={go} />
			<Nahodka id="nahodka" go={go} />
			<PeterburgPort id="peterburgPort" go={go} />

			<Kostroma id="kostroma" go={go} />
			<Yalta id="yalta" go={go} />
			<Sochi id="sochi" go={go} />
			
			<MosObl id="mosobl" go={go} />
			<LenObl id="lenobl" go={go} />
			<OmskObl id="omskobl" go={go} />
			<PermObl id="permobl" go={go} />
			<Baskortostan id="bask" go={go} />
			<NorilObl id="norobl" go={go} />
			<ChelaObl id="chebl" go={go} />
			<KrasnodarK_novoross id="kras_novor" go={go} />
			<KrasnodarK_sochi id="kras_sochi" go={go} />
			<PrimObl id="primobl" go={go} />
			<Krym id="krym" go={go} />
			<KostromObl id="kostobl" go={go} />
			<TumenObl id="tumobl" go={go} />

			<MosIndex id="mosindex" go={go} />
			<MoscKreml id="moscreml" go={go} />

			<SpbIndex id="spbindex" go={go} />
			<SpbZinger id="spbzinger" go={go} />
		</View>
	);
}

export default App;